import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, formValueSelector, reduxForm } from "redux-form";
import { createNumberMask } from "redux-form-input-masks";
import { getTimbangan } from "../../GetTimbangan";
import { getListBarang } from "../../actions/kodeBarangAction";
import { renderField, renderFieldSelect } from "../../renderField";
import { setFocus, SkeletonLoading } from "../../helper";

const currencyMask = createNumberMask({
  prefix: "Rp. ",
  suffix: ",-",
  locale: "id-ID",
});

class FormTambahBarang extends Component {
  constructor(props) {
    super(props);
    this.state = {
      listBarang: [],
      kodeJenis: "",
      kadarCetak: "",
      kadarBeli: "",
      non_aksesoris: "col-lg-12 row",
      aksesoris: "col-lg-12 row d-none",
    };
  }

  componentDidMount() {
    this.props.dispatch(getListBarang());
    getTimbangan(this.props.change);
  }

  handleChange = (input) => {
    var supp = input ? input : "DEFAULT | DEFAULT | DEFAULT | DEFAULT ";
    var result_explode = supp.split("|");
    console.log(result_explode[6]);
    if (result_explode[6] === "AKSESORIS") {
      this.setState({
        non_aksesoris: "col-lg-12 row d-none",
        aksesoris: "col-lg-12 row",
      });
    } else {
      this.setState({
        non_aksesoris: "col-lg-12 row ",
        aksesoris: "col-lg-12 row d-none",
      });
    }
    this.setState({
      kodeJenis: result_explode[0],
      kadarCetak: result_explode[1],
      kadarBeli: result_explode[2],
      kadarJual: result_explode[3],
      namaJenis: result_explode[4],
    });
    setFocus("Pkg (Gr)");
  };

  setKadar() {
    this.props.change("kadar", this.state.kadarCetak);
    this.props.change("kadarTransaksi", this.state.kadarBeli);
    this.props.change("kadarJual", this.state.kadarJual);
    this.props.change("keterangan", this.state.namaJenis);
  }
  setNetto() {
    this.props.change(
      "netto",
      this.props.netto === "NaN" ? 0 : this.props.netto
    );
  }
  render() {
    return (
      <form onSubmit={this.props.handleSubmit}>
        <div className="row">
          <div className="col-md-3 form-group">
            {String(this.props.KodeBarangList) !== "" ? (
              <Field
                name="kodeBarang"
                label="Kode Model"
                component={renderFieldSelect}
                onChange={(data) => this.handleChange(data)}
                onBlur={this.setKadar()}
                options={this.props.KodeBarangList.map((list) => {
                  let value = {
                    value:
                      list.kode_jenis +
                      "|" +
                      list.kadar_cetak +
                      "|" +
                      list.kadar_beli +
                      "|" +
                      list.kadar_jual +
                      "|" +
                      list.nama_jenis +
                      "|" +
                      list.kode_kategori +
                      "|" +
                      list.kode_kelompok,
                    name: list.nama_jenis,
                  };
                  return value;
                })}
              ></Field>
            ) : (
              <SkeletonLoading label="Kode Model" />
            )}
          </div>
          <div className="col-md-4 form-group">
            <Field
              name="keterangan"
              label="Keterangan"
              component={renderField}
              type="text"
              normalize={(value) => value && value.toUpperCase()}
            />
          </div>
          <div className="col-md-5">
            <div className={this.state.aksesoris}>
              <Field
                name="qty"
                label="Qty"
                component={renderField}
                type="text"
              />
            </div>
          </div>
          <div className={this.state.non_aksesoris}>
            <div className="col-md-2 form-group">
              <Field
                name="kadar"
                label="Cap Kode"
                component={renderField}
                type="text"
              />
            </div>
            <div className="col-md-2 form-group">
              <Field
                name="pkg"
                label="Pkg (Gr)"
                component={renderField}
                type="text"
                onBlur={this.setNetto()}
              />
            </div>
            <div className="col-md-2 form-group">
              <Field
                name="bruto"
                id="bruto"
                label="Bruto (Gr)"
                component={renderField}
                type="text"
                onBlur={this.setNetto()}
              />
            </div>
            <div className="col-md-2 form-group">
              <Field
                name="kadarTransaksi"
                label="Kode Awal (%)"
                component={renderField}
                type="text"
                readOnly={true}
              />
            </div>
            <div className="col-md-2 form-group">
              <Field
                name="netto"
                label="Netto (Gr)"
                component={renderField}
                type="text"
              />
            </div>
            <div className="col-md-4 form-group">
              <label htmlFor="">Ongkos (Rp)</label>
              <Field
                name="ongkos"
                label="Ongkos (Rp)"
                component="input"
                type="telp"
                className="form-control"
                defaultValue={0}
                {...currencyMask}
              />
            </div>
          </div>
          <div className="col-md-12 form-group">
            <div className="text-right">
              <button
                className="btn btn-primary"
                type="submit"
                onClick={() => document.getElementById("Kode Model").focus()}
              >
                Simpan
              </button>
            </div>
          </div>
        </div>
      </form>
    );
  }
}

FormTambahBarang = reduxForm({
  form: "FormTambahBarang",
  enableReinitialize: true,
})(FormTambahBarang);
const selector = formValueSelector("FormTambahBarang"); // <-- same as form name
export default connect((state) => {
  const { bruto, kadarTransaksi } = selector(state, "bruto", "kadarTransaksi");
  return {
    KodeBarangList: state.provinsi.kodeBarang,
    netto: (parseFloat(bruto) * (parseFloat(kadarTransaksi) / 100)).toFixed(3),
    initialValues: {
      discGlobalPr: 0,
      p: 0,
      k: 0,
      pkg: 0,
      bruto: 0,
    },
  };
})(FormTambahBarang);
